import React from 'react';
import { CardImg, CardTitle, CardSubtitle, Breadcrumb, BreadcrumbItem, Card, CardBody, CardHeader, Media } from 'reactstrap';
import { Link } from 'react-router-dom';
import {Loading} from './LoadingComponent'
import {TEACHERS} from '../shared/teachers'



function About(props) {

    function RenderLeader({ teacher }) {
        return (
                <Card className="card_colour">
                    <CardImg width="100%" src={teacher.image} alt={teacher.name} />
                    <CardTitle>{teacher.name}</CardTitle>
                    <CardTitle >{teacher.designation}</CardTitle>
                    <CardSubtitle >{teacher.description}</CardSubtitle>
                </Card>

        );
    }


    const teachers = props.teachers.teachers.map((teacher) => {
        return (
            <div className="col-12 col-md-5 m-1">
                <RenderLeader teacher={teacher} />
            </div>

        );
    });
    if (props.teachers.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.teachers.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h4>{props.teachers.errMess}</h4>
                    </div>
                </div>
            </div>
        );
    }

    else


        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>About Us</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>About Us</h3>
                        <hr />
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12 col-md-6">
                        <h2>Our Mission</h2>
                        <h5>We aim to impart Computer Science knowledge to people because of how important it will become in the future.</h5>
                        <br></br>
                        <h5>Why <strong>Code Coach</strong>?  Simply because we offer affordable, personalised and convenient lessons taught by only the best in the industry.</h5>
                    </div>
                    <div className="col-12 col-md-5">
                        <Card>
                            <CardHeader className="bg-primary text-white">Facts about Code Coach</CardHeader>
                            <CardBody>
                                <dl className="row p-1">
                                    <dt className="col-6">Started</dt>
                                    <dd className="col-6">3 Feb. 2020</dd>
                                    <dt className="col-6">Major Stake Holder</dt>
                                    <dd className="col-6">Barney Stinson</dd>
                                    <dt className="col-6">Last Year's Turnover</dt>
                                    <dd className="col-6">$1,250,375</dd>
                                    <dt className="col-6">Employees</dt>
                                    <dd className="col-6">4</dd>
                                </dl>
                            </CardBody>
                        </Card>
                    </div>
                    <div className="col-12 mt-5">
                        <Card>
                            <CardBody className="bg-faded">
                                <blockquote className="blockquote">
                                    <p className="mb-0">"Never Hate Your Enemies. It Affects Your Judgment."</p>
                                    <footer className="blockquote-footer">Michael Corleone,
                                <cite title="Source Title">The Godfather</cite>
                                    </footer>
                                </blockquote>
                            </CardBody>
                        </Card>
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12">
                        <h2>Star tutors</h2>
                    </div>

                    {teachers}

                </div>
                
            </div>
        );
}

export default About;